.blog-container {
  display: flex;
  flex-direction: column;
}
.blog-header {
  width: 100%;
  text-align: left;
  margin-bottom: 5rem;
}
.blog-header h1 {
  font-size: 62px;
  line-height: 75px;
  font-family: var(--font-base);
  font-weight: 800;
}
.blog-content-container {
  display: flex;
  flex-direction: row;
}
.blog-content-boxA {
  flex: 0.75;
  margin-right: 2rem;
}
.blog-content-boxB {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
@media screen and (max-width: 1150px) {
  .blog-content-container {
    flex-direction: column-reverse;
  }
  .blog-content-boxA {
    margin: 2rem 0;
  }
}
@media screen and (max-width: 700px) {
  .blog-content-boxB {
    grid-template-columns: repeat(1, 1fr);
  }
  .blog-header h1 {
    font-size: 46px;
    line-height: 52px;
  }
}
@media screen and (max-width: 550px) {
  .blog-header h1 {
    font-size: 34px;
    line-height: 42px;
  }
}
